

























.groups {
	background: #FFFFFF;
	margin-top: 20px;
	padding: 20px;
	border: 1px solid #DDD;
	border-radius: 10px;
	box-shadow: 1px 1px 2px #ccc;
}

.btn-wrapper {
	margin-top: 15%;
}

.left-part {
	width: 45%;
	float: left;
}

.right-part {
	width: 45%;
	float: right;
}

.btn {
	width: 100%;
	line-height: 3;
	color: white;
	border-radius: 6px;
	font-size: 18px;
	border: none;
}
.btn-primary {
	background-color: #04BE02;
}
.btn-disable {
	background-color: #ccc;
}



